import React, { useState, useEffect } from "react";

import { Tabs, Tab, Paper, Box, Typography } from "@mui/material";
import NavBar from "../components/NavBar";
import Login from "../components/login";
import SignUp from "../components/signup";
import { useNavigate } from "react-router-dom";

const TabPanel = (props) => {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

const SignInUp = () => {

    const paperStyle = { width: 420, margin: "5vh auto", backgroundColor: "#cccccc", borderRadius: "20px" }
    const tabStyling = { width: 210, fontWeight: "700", fontSize: "1em" }

    const [value, setValue] = useState(0);

    const handleChange = (event, IndexValue) => {
        setValue(IndexValue);
    };

    const navigate = useNavigate();

    const [error, setError] = useState("");

    useEffect(() => {
        if (localStorage.getItem("LoggedIn") === "true") {
            navigate("/profile");
        }
    }, []);

        const handleLogin = (details) => {
            console.log(details);

            if (details.username === 'admin' && details.password === 'admin') {
                console.log("Logged In");
                localStorage.setItem("LoggedIn", true);
                localStorage.setItem("username", details.username);
                navigate("/profile");
            }
            else {
                console.log("Details do not match!");
                setError("Details do not match!");
            }
        }

        return (
            <>
                <NavBar />
                <Paper elevation={10} style={paperStyle}>
                    <Tabs value={value} onChange={handleChange} indicatorColor="secondary" textColor="secondary" >
                        <Tab label="Sign In" style={tabStyling} />
                        <Tab label="Sign Up" style={tabStyling} />
                    </Tabs>
                    <TabPanel value={value} index={0}>
                        <Login handleChange={handleChange} handleLogin={handleLogin} error={error} />
                    </TabPanel>
                    <TabPanel value={value} index={1}>
                        <SignUp handleChange={handleChange} />
                    </TabPanel>
                </Paper>
            </>
        );
    }

export default SignInUp;