import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Reddit from '@mui/icons-material/Reddit';
import Grid2 from '@mui/material/Unstable_Grid2/Grid2';

function NavBar() {

    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleMenu = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    return (

        <AppBar position="static" color='secondary' style={{ opacity: '0.8' }}>
            <Container maxWidth="xl" style={{ marginLeft: '0px' }}>
                <Grid2 container wrap >
                    <Toolbar disableGuttersd >
                        <Reddit style={{ fontSize: '35px', marginTop: '-5px' }} sx={{ display: { xs: 'none', md: 'flex' }, mr: 1, color: 'rgb(255,45,45)' }} />
                        <Typography
                            variant="h5"
                            noWrap
                            component="a"
                            href="/profile"
                            sx={{
                                mr: 2,
                                display: { xs: 'none', md: 'flex' },
                                fontFamily: 'Lato',
                                fontWeight: 900,
                                letterSpacing: '.1rem',
                                color: 'rgb(255,45,45)',
                                textDecoration: 'none',
                            }}
                            style={{
                                fontSize: '1.5em',
                            }}
                        >
                            Greddiit
                        </Typography>
                        <Reddit style={{ fontSize: '35px', marginTop: '-5px' }} sx={{ display: { xs: 'flex', md: 'none' }, mr: 1, color: 'rgb(255,45,45)' }} />
                        <Typography
                            variant="h5"
                            noWrap
                            component="a"
                            href=""
                            sx={{
                                mr: 2,
                                display: { xs: 'flex', md: 'none' },
                                flexGrow: 1,
                                fontFamily: 'Lato',
                                fontWeight: 900,
                                letterSpacing: '.1rem',
                                color: 'rgb(255,45,45)',
                                textDecoration: 'none',
                            }}
                            style={{
                                fontSize: '1.5em',
                            }}
                        >
                            Greddiit
                        </Typography>

                    </Toolbar>
                    {/* <div>
                        <IconButton
                            size="large"
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            aria-haspopup="true"
                            onClick={handleMenu}
                            color="inherit"
                        >
                            <AccountCircle />
                        </IconButton>
                        <Menu
                            id="menu-appbar"
                            anchorEl={anchorEl}
                            anchorOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                            keepMounted
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                            open={Boolean(anchorEl)}
                            onClose={handleClose}
                        >
                            <MenuItem onClick={handleClose}>Profile</MenuItem>
                            <MenuItem onClick={handleClose}>My account</MenuItem>
                        </Menu>
                    </div> */}
                </Grid2>
            </Container>
        </AppBar>
    );
}
export default NavBar;