import { Grid, Paper, Typography } from "@mui/material";
import React from "react";
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import Avatar from "@mui/material/Avatar";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Grid2 from "@mui/material/Unstable_Grid2/Grid2";
import Link from "@mui/material/Link";
import Checkbox from "@mui/material/Checkbox";

const Signup = ({ handleChange }) => {

    const paperStyle = { padding: 30, height: '51em', width: 360, margin: "0px", backgroundColor: "#cccccc", borderRadius: "20px" }
    const avatarStyle = { margin: 20, backgroundColor: 'rgb(24,83,72)', width: "80px", height: "80px" }
    const avatarIconStyle = { fontSize: "50px" }
    const textFieldStyle = { margin: "12px auto", backgroundColor: "#ffffff", borderRadius: "5px" }
    const textFieldStyle2 = { margin: "12px auto", width: "100px", backgroundColor: "#ffffff", borderRadius: "5px" }
    const textFieldStyle3 = { margin: "12px auto", width: "250px", backgroundColor: "#ffffff", borderRadius: "5px" }
    const buttonStyle = { margin: "20px auto 30px auto", backgroundColor: 'rgb(24,83,72)', fontWeight: "600" }
    const typographyStyle = { margin: "-5px auto", color: 'rgb(248,83,83)', fontSize: "18px", fontWeight: "600" }
    const typographyStyle2 = { margin: "10px auto" }

    return (
        <Grid>
            <Paper style={paperStyle}>
                <Grid align="center">
                    <Typography component={'span'} variant='h6' style={typographyStyle} >SIGN UP</Typography>
                    <Avatar style={avatarStyle} ><AccountCircleIcon style={avatarIconStyle} /></Avatar>
                </Grid>

                <Grid2 container wrap columnGap={2.5}>
                    <TextField variant='filled' type='text' label='First Name' placeholder='Enter your First Name' required style={textFieldStyle} />
                    <TextField variant='filled' type='text' label='Second Name' placeholder='Enter your Second Name' required style={textFieldStyle} />
                </Grid2>

                <TextField variant='filled' type='text' label='Username' placeholder='Enter your Username' fullWidth required style={textFieldStyle} />
                <TextField variant='filled' type='email' label='Email ID' placeholder='Enter your email ID' fullWidth required style={textFieldStyle} />

                <Grid2 container wrap columnGap={2.5}>
                    <TextField variant='filled' type='number' label='Age' placeholder='Age' required style={textFieldStyle2} />
                    <TextField variant='filled' type='tel' label='Contact' placeholder='Contact' required style={textFieldStyle3} />
                </Grid2>

                <TextField variant='filled' type='password' label='Set Password' placeholder='Enter Password' fullWidth required style={textFieldStyle} />
                <TextField variant='filled' type='password' label='Confirm Password' placeholder='Confirm Password' fullWidth required style={textFieldStyle} />
                <Grid2 container>
                    <Typography component={'span'}>
                        <Checkbox />
                        I accept the <Link href="#">Terms and Conditions</Link>
                    </Typography>
                </Grid2>
                <Button elevation={15} type="submit" color="primary" variant="contained" fullWidth style={buttonStyle}>Sign up</Button>

                <Typography style={typographyStyle2}>Already have an account? <Link href="#" onClick={() => handleChange("event", 0)}>Sign in</Link>
                </Typography>
            </Paper>
        </Grid>
    );
}

export default Signup;