import { Grid, Paper, Typography } from "@mui/material";
import React, { useState } from "react";
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import Avatar from "@mui/material/Avatar";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Link from "@mui/material/Link";

const Login = ({ handleChange, handleLogin, error, LoggedIn }) => {

    const paperStyle = { padding: 30, height: '29em', width: 360, margin: "0px", backgroundColor: "#cccccc", borderRadius: "20px" }
    const avatarStyle = { margin: 20, backgroundColor: 'rgb(24,83,72)', width: "80px", height: "80px" }
    const avatarIconStyle = { fontSize: "50px" }
    const textFieldStyle = { margin: "12px auto", backgroundColor: "white", borderRadius: "5px" }
    const buttonStyle = { margin: "40px auto", backgroundColor: 'rgb(24,83,72)', fontWeight: "600" }
    const typographyStyle = { margin: "-5px auto", color: 'rgb(248,83,83)', fontSize: "18px", fontWeight: "600" }
    const [details, setDetails] = useState({ username: "", password: "" });

    const submitHandler = e => {
        e.preventDefault();
        handleLogin(details);
        setDetails({ username: "", password: "" });
    }

    return (
        <Grid>
            <form onSubmit={submitHandler}>
                <Paper style={paperStyle}>
                    <Grid align='center'>
                        <Typography component={'span'} variant='h6' style={typographyStyle}>SIGN IN</Typography>
                        <Avatar style={avatarStyle}><AccountCircleIcon style={avatarIconStyle} /></Avatar>
                    </Grid >
                    <TextField
                        variant='filled' 
                        type='text' 
                        label='Username' 
                        placeholder='Enter your Username' 
                        fullWidth 
                        required 
                        style={textFieldStyle} 
                        onChange={e => setDetails({...details, username: e.target.value})} 
                        value={details.username} />
                    
                    <TextField
                        variant='filled' 
                        type='password' 
                        label='Password' 
                        placeholder='Enter your Password' 
                        fullWidth 
                        required 
                        style={textFieldStyle} 
                        onChange={e => setDetails({...details, password: e.target.value})} 
                    value={details.password} />

                    <Button elevation={15} 
                        type="submit" 
                        color="primary" 
                        variant="contained" 
                        fullWidth 
                        style={buttonStyle} >Log in</Button>

                    <Typography 
                        component={'span'}>Don't have an account? <Link href="#" onClick={() => handleChange("event", 1)}>Sign up</Link>
                    </Typography>
                </Paper>
            </form>
        </Grid>
    );
}

export default Login;