import { Grid, Paper, TextField, Button } from "@mui/material";
import React, { useEffect } from "react";
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import Avatar from "@mui/material/Avatar";
import Grid2 from "@mui/material/Unstable_Grid2/Grid2";
import Typography from "@mui/material/Typography/Typography";
import EditIcon from '@mui/icons-material/Edit';
import NavBar from "../components/NavBar";
import Followers from "../components/followers";
import Following from "../components/following";
import { useNavigate } from "react-router-dom";
import NavBar2 from "../components/NavBar2";

export default function Profile() {

    const paperStyle = { padding: 30, height: '32em', width: 480, margin: "8vh auto", backgroundColor: "#cccccc", borderRadius: "20px" }
    const avatarStyle = { margin: "20px 0px 20px 0px", backgroundColor: 'rgb(24,83,72)', width: "100px", height: "100px", borderColor: "light-green", borderWidth: "2px", borderStyle: "solid" }
    const avatarIconStyle = { fontSize: "70px" }
    const usernameStyle = { color: 'rgb(44,101,77)', fontSize: "2em", marginBottom: "20px" }
    const TextFieldStyle = { margin: "12px auto", backgroundColor: 'white', borderRadius: "5px", width: "300px" }
    const TextFieldStyle2 = { margin: "12px auto", backgroundColor: 'white', borderRadius: "5px", width: "140px" }
    const TextFieldStyle3 = { margin: "12px auto", backgroundColor: 'white', borderRadius: "5px", width: "320px" }
    const editAvatarStyle = { width: "30px", height: "30px", borderRadius: "50%", color: 'rgb(248,83,83)' }
    const editButtonStyle = { marginLeft: "330px", backgroundColor: 'white', width: "40px", height: "40px", borderColor: "light-green", borderWidth: "2px", borderStyle: "solid" }

    const navigate = useNavigate();

    useEffect(() => {
        if (!localStorage.getItem("LoggedIn")) {
            navigate("/");
        }
    }, []);
    
    const handleLogout = () => {
        console.log("Logout");
        localStorage.removeItem("LoggedIn");
        localStorage.removeItem("username");
        navigate("/");
    }

    return (
        <>
            <NavBar2 />
            <Grid align='center'>
                <Paper elevation={20} style={paperStyle}>
                    <Grid display="inline-flex">
                        <Avatar style={avatarStyle} ><AccountCircleIcon style={avatarIconStyle} /></Avatar>
                        <Avatar style={editButtonStyle}>
                            <Button ><EditIcon style={editAvatarStyle} /></Button>
                        </Avatar>
                    </Grid>
                    <Typography component={'span'} variant="h5" style={usernameStyle}>admin</Typography>

                    <Grid2 container wrap columnGap={2.5} style={{ marginTop: '20px' }}>
                        <TextField variant="filled" label='First Name' value='admin' InputProps={{ readOnly: true }} readOnly style={TextFieldStyle} />
                        <TextField variant="filled" label='Second Name' value='admin' InputProps={{ readOnly: true }} readOnly style={TextFieldStyle} />
                    </Grid2>

                    <Grid2 container wrap columnGap={2.5}>
                        <TextField variant="filled" label='Username' value='admin' InputProps={{ readOnly: true }} readOnly fullWidth style={TextFieldStyle} />
                        <TextField variant="filled" type='email' label='Email ID' value='admin@admin.in' InputProps={{ readOnly: true }} readOnly fullWidth style={TextFieldStyle} />
                    </Grid2>

                    <Grid2 container wrap columnGap={2.5}>
                        <TextField variant="filled" type='number' label='Age' value='18' InputProps={{ readOnly: true }} readOnly style={TextFieldStyle2} />
                        <TextField variant="filled" type='tel' label='Contact' value='0123456789' InputProps={{ readOnly: true }} readOnly style={TextFieldStyle3} />
                    </Grid2>

                    <Grid display='inline-flex' align='center' columnGap={2.5} >
                        <Followers />
                        <Following />
                    </Grid>
                    {/* <Button onClick={handleLogout} >Log Out</Button> */}
                </Paper>
            </Grid >
        </>
    );
}
