import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';

const Following = ['Mark', 'Stephen', 'Adrian', 'Christopher', 'Chloe', 'Steven', 'Andrew', 'Randy', 'Miguel', 'Eileen', 'Deborah', 'Ryan']

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 100,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    borderRadius: 2,
    boxShadow: 24,
    p: 4,
};

export default function BasicModal() {
    const [open, setOpen] = React.useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    return (
        <div>
            <Button className='followingButton' onClick={handleOpen}>Following</Button>
            <Modal
                open={open}
                onClose={handleClose}
            >
                <Box sx={style}>
                    <Typography component={'span'} className='followingHead' variant="h6">
                        Following
                    </Typography>
                    <Typography component={'span'} className='followingList' sx={{ mt: 2 }}>
                        {Following.map((item) => (
                            <div>{item}</div>
                        ))}
                    </Typography>
                </Box>
            </Modal>
        </div>
    );
}