import React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';

const Followers = ['Leslie', 'Gloria', 'Laura', 'Brian', 'Carrie', 'Kimberly', 'Elizabeth', 'Anna']

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 100,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  borderRadius: 2,
  boxShadow: 24,
  p: 4,
};

export default function BasicModal() {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <div>
      <Button className='followersButton' onClick={handleOpen} >Followers</Button>
      <Modal
        open={open}
        onClose={handleClose}
      >
        <Box sx={style}>
          <Typography component={'span'} className='followersHead' variant="h6">
            Followers
          </Typography>
          <Typography component={'span'} className='followersList' sx={{ mt: 2 }}>
            {Followers.map((item) => (
              <div>{item}</div>
            ))}
          </Typography>
        </Box>
      </Modal>
    </div>
  );
}